.MainContainer {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.IntroTextContainer {
    text-align: center;
    max-width: 80%;
}

.IntroTextContainer>h1 {
    text-transform: uppercase;
}

.IntroTextContainer>p {
    background-color: #F8EBBF;
    padding: 6px;
    border-radius: 5px;
}

.ProjectsContainer {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    align-items: center;
    gap: 20px;
    width: 90%;
}

.ProjectsContainer>div {
    background-color: #073648;
    width: 300px;
    height: 170px;
    margin: 10px;
    border-radius: 6px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: 4px 5px 8px #343434;



}

.ProjectsContainer>div>h2,
.ProjectsContainer>div>p {
    color: #d9d9d9;
    font-size: 105%;
}

.ProjectsContainer>div>p {
    text-align: center;
    width: 90%;

}

.ProjectsContainer>div:hover {
    background-color: #094259;
    width: 320px;
    height: 190px;
    margin: 0px;

}

.ProjectsContainer>div:hover>h2 {
    font-size: 115%;
}

.ProjectsContainer>div:hover>p {
    font-size: 105%;
}

.ProjectsContainer>div:hover p,
.ProjectsContainer>div:hover h2 {
    color: #fff;
}

.popupContainer {
    height: 100%;
    width: 100%;
    left: 0;
    top: 0;
    overflow: hidden;
    position: fixed;
    background-color: #00000050;
}
.popupSpan{
    position: absolute;
    width: 0;
    height: 0;
    margin: 0;
    padding: 0;
}

@keyframes anvil {
    0% {
        transform: scale(1) translateY(0px);
        opacity: 0;
        box-shadow: 0 0 0 rgba(241, 241, 241, 0);
    }

    1% {
        transform: scale(0.96) translateY(10px);
        opacity: 0;
        box-shadow: 0 0 0 rgba(241, 241, 241, 0);
    }

    100% {
        transform: scale(1) translateY(0px);
        opacity: 1;
        box-shadow: 0 0 500px rgba(241, 241, 241, 0);
    }
}


.popupContainer>div {
    position: relative;
    margin-top: 10%;
    margin-bottom: auto;
    margin-left: auto;
    margin-right: auto;
    background-color: #073648;
    height: 500px;
    width: 700px;
    border-radius: 6px;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: hidden;
    -webkit-animation: anvil 0.3s cubic-bezier(0.38, 0.1, 0.36, 0.9) forwards;

}

.popupContainer>div>h1 {
    color: #fff;
}

.popupContainer>div>button {
    position: fixed;
    background-color: rgba(255, 255, 255, 0);
    color: #fff;
    border-width: 0;
    font-size: larger;
    font-weight: bolder;
    top: 2%;
    right: 2%;
}

.popupContainer>div>button:hover {
    color: #000
}

.popupProjectContainer {
    background-color: #fff;
    width: 90%;
    height: 75%;
    border-radius: 3px;
    overflow-y: auto;
}

.popupProjectContainer::-webkit-scrollbar {
    width: 10px;
    height: 10px;
}


.popupProjectContainer::-webkit-scrollbar-track {
    border-radius: 3px;
    background-color: rgba(179, 179, 179, 0.434);
}


.popupProjectContainer::-webkit-scrollbar-thumb {
    background: #073648;

    border-radius: 3px;
    border-color: rgba(179, 179, 179, 0.434);
}

.popupProjectContainer::-webkit-scrollbar-thumb:hover {
    background: #09435a;
}

.popupProjectContainer>div {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}



.slotMachine {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    border-style: solid;
    width: 330px;
    height: 120px;
    margin-left: auto;
    margin-right: auto;
}

.slotMachine>p {
    padding: 0%;
    margin: 0;
    margin-bottom: 3px;
}

.slotMachine>div {
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 5%;
}

.slotMachine>div>img {
    width: 65px;
    height: 65px;
    border-radius: 10px;
    border-width: 2px;
    border-style: solid;
    width: 40%;
}

.slotMachine>div>button {
    width: 65px;
    height: 65px;
    border-radius: 10px;
    border-width: 2px;
    border-style: solid;

    font-size: larger;
}

.slotMachine>div>button:hover {

    background-color: crimson;
    color: #fff;

}
@media screen and (max-width: 795px) {
    .popupContainer>div {
        width: 90%;
        height: 60%;
    }
}